<script>
import { onMounted, ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import addico from '../assets/sections/expand.svg'
import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      pageModel: {
        current: 1,
        size: 20,
      },
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },

      totalSupply: 5000,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 0,
      mintNumber: "",
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
        tip7:false,
        tip8:false,
        tip9:false,
        tip10:false,
        tip11:false,
        tip12:false,
        tip13:false,
        tip14:false,
        tip15:false,
        tip16:false,
        tip17:false,
        tip118:false,
        tip119:false,
      },
      imgAdd:addico,
      imgDel:delico,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;

    // 地址省略显示
    const omitshow = function (value, start, end) {
      if (value != "" && value != null && typeof value != "undefined") {
        var str =
          value.substring(0, start) +
          "..." +
          value.substring(value.length - end);
        return str;
      }
    };
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    //获取已经铸造数量
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(
          state.connect.address
        );
        let amount = state.mintNumber * state.mintPrice;

        if (Number(balance) < amount) {
          alert("Insufficient wallet balance");
          console.log(balance, amount);
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    let fapList = ref([
      {title: 'IP', content: 'The owner of a QuirkiesTron owns the art IP. They may use the image as they wish so long as they own that particular QuirkiesTron. Use of the image for profit must cease upon sale. Although the holder owns the IP they do not own any QuirkiesTron logos or other art used and therefore cannot use it without written approval.'},
      {title: 'Rarity', content: 'We have intentionally not officially listed on any rarity website as we believe the community should base the value of a QuirkiesTron on its art not rarity. We have seen this play out, with the community valuing some more common traits higher due to their overall desirability. This keeps the art at the forefront of the collection rather than rarity scores.'},
      {title: 'Vision and Culture', content: 'We are an art-based project with a focus on community and culture. We are inspired by our founders love of the surf/skate culture and look to implement this throughout our brand. We are above all else entirely inclusive and will always celebrate our own and everyone else\'s quirks.'}
    ]);
    const methods = {
      //
    };
    onMounted(() => {
      setTimeout(async function () {
        getTotalSupply();
      }, 1000);
    });
    return {
      ...toRefs(state), // 将 state 返回出去，就可以直接使用 state 里面的属性
      ...methods,
      connectWallet,
      disconnect,
      getTotalSupply,
      omitshow,
      getMint,
      fapList
    };
  },
};
</script>



